  background-color: $background-color;\n}\n\nh1, h2, h3, h4, p {\n  margin: 0;\n}\n\nh2 {\n  font-size: 1.2rem;\n}\n\n.container {\n  left: 280px;\n  width: calc(100% - 280px);\n  position: relative;\n  min-height: 100vh;\n  min-height: 100svh;\n  height: 100%;\n  display: grid;\n  grid-template-columns: 1fr;\n  grid-template-rows: auto 1fr auto;\n  grid-template-areas: \"header\" \"main\" \"footer\";\n\n  @media screen and (max-width: 700px) {\n    left: 0;\n    width: initial;\n  }\n\n  &--menu-hidden {\n    left: 0;\n    width: initial;\n\n    & .header__menu {\n      display: block;\n    }\n  }\n}\n\n.header {\n  position: relative;\n  grid-area: header;\n  padding: 1rem;\n  background-color: $color-3;\n\n  &__menu {\n    cursor: pointer;\n    display: none;\n    position: absolute;\n    top: 50%;\n    transform: translateY(-50%);\n  }\n\n  &__title {\n    color: $background-color;\n    text-shadow: 0 0 5px $text-color;\n    text-align: center;\n    font-size: 1.5rem;\n  }\n}\n\n.content {\n  grid-area: main;\n  padding: 1rem;\n}\n\n.footer {\n  background-color: $color-3;\n  color: $background-color;\n  grid-area: footer;\n  padding: 1rem;\n\n  &__made-by {\n    text-align: center;\n  }\n\n  & a, & a:visited {\n    @include links-on-accent;\n  }\n}\n\n.styled-input {\n  border: 2px solid $color-3;\n  border-radius: 4px;\n  padding: 0.4rem 0.8rem;\n  font-size: 1rem;\n\n  &::placeholder {\n    color: $color-3;\n    opacity: 0.5;\n  }\n}\n\n.button {\n  min-width: 120px;\n  background-color: $color-3;\n  padding: 0.5rem 1rem;\n  color: $background-color;\n  border: none;\n  border-radius: 6px;\n  font-size: 1rem;\n  cursor: pointer;\n  transition: filter ease-in-out 400ms;\n\n  &:hover {\n    filter: brightness(1.2) drop-shadow(0 0 2px $text-color);\n  }\n\n  &:active {\n    filter: brightness(0.95);\n  }\n}\n\n.buttons-wrapper {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 1rem;\n}\n\n.input-wrapper {\n  display: flex;\n  align-items: center;\n  column-gap: 0.5rem;\n}\n\n.input-column-wrapper {\n  display: flex;\n  flex-direction: column;\n  gap: 0.5rem;\n\n  & label {\n    font-weight: bold;\n  }\n}\n\n.modal {\n  border: none;\n  width: clamp(320px, 50vw, 640px);\n\n  &::backdrop {\n    backdrop-filter: blur(4px) brightness(0.8);\n  }\n}","$background-color: #E7F6F2;\n$color-2: #A5C9CA;\n$color-3: #395B64;\n$text-color: #2C3333;","@use 'variables' as *;\n\n@mixin links-on-accent {\n  color: $color-2;\n  text-decoration: underline;\n  text-decoration-style: dashed;\n  text-decoration-thickness: 1px;\n  text-underline-offset: 4px;\n  text-decoration-color: $background-color;\n\n  &:hover {\n    filter: brightness(1.1);\n    text-decoration-style: solid;\n    text-decoration-thickness: 3px;\n    text-underline-offset: 6px;\n  }\n}\n\n@mixin reset-list {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n@mixin tags-container {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 0.5rem;\n  margin-bottom: 1rem;\n}\n\n@mixin tag {\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  background-color: $background-color;\n  padding: 2px 6px;\n  border-radius: 8px;\n  font-size: 0.9rem;\n\n  &--selected {\n    background-color: $color-3;\n    color: $background-color;\n  }\n\n  &::before {\n    content: '#';\n  }\n}\n\n@mixin edit {\n  cursor: pointer;\n  width: 1.3rem;\n  height: 1.3rem;\n  border: none;\n  font-size: 0;\n  background: url('../../assets/edit.svg') center center no-repeat;\n  transition: transform ease-in 300ms;\n\n  &:hover {\n    transform: scale(1.1) translateY(-4px);\n  }\n}\n\n@mixin control-button {\n  cursor: pointer;\n  background-color: transparent;\n  background-size: contain;\n  background-position: center;\n  background-repeat: no-repeat;\n  opacity: 0.4;\n  width: 2rem;\n  height: 2rem;\n  border: none;\n  font-size: 0;\n}"],"sourceRoot":""}]);
